.About {
    background-color: #fff;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

#about-heading {
    font-size: calc(20px + 2vmin);
    color: #282c34;
    margin: 0;
}

.content {
    width: 100vw;
    display: flex;
    flex-direction: column;
}

img {
    max-width: 20vw;
    height: auto;
    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
    margin: 10em;
}

